.imagesWrapper{
    top:0;
    left:0;
    position: absolute;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
}

.header {
    text-align: right;
    box-sizing: border-box;
    text-transform: uppercase;
    width: 60%;
    color:#211f1f;
    font-family: SecularOne;
    font-size: 25px;
    display: block;
    background: #65fd9b;
    margin:0;
    border-radius: 0 10px 10px 0;
    padding: 10px;
    padding-right:5%;
    transform: translateY(-30%);
}

.backpackItemsWrapper {
    display: flex;
    width: 100%;
    height: 80%;
    margin: 20px auto;
    justify-content: center;
    align-items: center;
}

.backpack {
    height: 80%;
    width: 35%;
    border-radius: 0 20px 20px 0;

    background-image: url("../../../../assets/images/secondSlide/backpcktest.jpg");
    background-position: right;
    background-repeat: no-repeat;
    background-size: cover;
}

.items {
    position: relative;
    width: 65%;
    height: 80%;
    filter:grayscale(1);
}

@media screen and (max-width: 1234px) {
    .backpack{
        width: 50%;
    }
}

@media screen and (max-width: 930px) {
    .backpack{
        width: 60%;
    }
}

@media screen and (max-width: 800px) {
    .backpack{
        width: 70%;
    }
}

@media screen and (max-width: 700px) {
    .backpack{
        width: 100%;
    }
}


@media screen and (max-width: 600px) {
    .backpack{
        width: 250%;
    }
}