.CarouselSection {
    width: 100%;
    height: 800px;
    position: relative;
    user-select: none;
    background: #FFF6F8;
}

.header {
    width: 40%;
    float: right;
    padding: 10px 40px;
    margin-top: 0;
    box-shadow: 0 0 10px #FFF6F8;
    border-radius: 10px 0 0 10px;
    transform: translateY(-20px);
    text-transform: uppercase;
    font-family: SecularOne;
    font-size: 25px;
    color: #211f1f;
    background: #65fd9b;
}

.carouselWrapper {
    position: absolute;
    top: 10%;
    height: auto;
    width:auto;
    transition: 1s ease-out;
}

@media screen and (max-width: 1600px){
    .CarouselSection{
        height: 700px;
    }
}

@media screen and (max-width: 1234px){
    .CarouselSection{
        height: 600px;
    }

    .carouselWrapper{
        top:20%;
    }
}